import React, { Suspense, lazy } from "react";
import style from "./DepartmentHead.module.css";
import Backlogs from "./Backlogs/Backlogs";
import Examinations from "./Exminations/Exminations";
import DepartmentHeadProfile from "./DepartmentHeadProfile/DepartmentHeadProfile";
import { useDepartmentProfile } from "../../../../hooks/member_tab/department-api";
// import DepartmentSetting from "./DepartmentSetting/DepartmentSetting";
// import OneDepartmentClass from "./OneDepartmentClass";
// import Holiday from "./Holiday/Holiday";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
const StaffRoom = lazy(() => import("./staff/StaffRoom"));
const Checklist = lazy(() => import("./Checklist/Checklist"));
const Fees = lazy(() => import("./Fees/Fees"));
const DepartmentMentor = lazy(() =>
  import("./DepartmentMentor/DepartmentMentor")
);
const OneDepartmentClassDetail = lazy(() =>
  import("./OneDepartmentClassDetail/OneDepartmentClassDetail")
);
const DepartmentComplaint = lazy(() =>
  import("./DepartmentSetting/DepartmentComplaint")
);
const DepartmentExamSetting = lazy(() =>
  import("./DepartmentSetting/DepartmentExamSetting")
);
const DepartmentPo = lazy(() => import("./DepartmentPO/DepartmentPo"));
const DepartmentHoliday = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/DepartmentManager/DepartmentHoliday/DepartmentHoliday"
  )
);
const DepartmentGoodsStore = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/DepartmentManager/DepartmentGoodsStore/DepartmentGoodsStore"
  )
);
const StudentStatitics = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/FinanceManager/StudentStatitics/StudentStatitics"
  )
);
const UserMemberContainerWrapper = lazy(() =>
  import("../../../../optimized/Ui/UserMember/UserMemberContainerWrapper")
);
const UserMemberContentWrapper = lazy(() =>
  import("../../../../optimized/Ui/UserMember/UserMemberContentWrapper")
);
const StudentStatiticsDetail = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/FinanceManager/StudentStatitics/StudentStatiticsDetail"
  )
);
const QvipleLoader = lazy(() =>
  import("../../../../optimized/Loader/QvipleLoader")
);
const UserMemberMainWrapper = lazy(() =>
  import("../../../../optimized/Layout/UserMemberTab/UserMemberMainWrapper")
);
const DepartmentOngoingApplication = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/AcademicDepartmentManager/DepartmentOngoingApplication/DepartmentOngoingApplication"
  )
);
const ApplicationSubjectListDetail = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/AdmissionManager/OneOngoingAdmission/ApplicationSubjectList/ApplicationSubjectListDetail"
  )
);
const OneOngoingDepartmentConfig = lazy(() =>
  import(
    "../../../../optimized/UserStaffMember/AcademicDepartmentManager/DepartmentOneOngoingApplication/OneOngoingDepartmentConfig"
  )
);
function DepartmentHead({ onClickValue, carryParentState }) {
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();

  const { departmentProfile, departmentProfileRefetch } = useDepartmentProfile({
    did: onClickValue?._id,
    skip: !onClickValue?._id,
  });
  React.useEffect(() => {
    if (onClickValue?._id) departmentProfileRefetch();
  }, [onClickValue?._id, departmentProfileRefetch]);

  // console.info("onClickValue", departmentProfile);
  return (
    <Suspense fallback={<QvipleLoader />}>
      {searchParams.get("a") === "ongoing" ? (
        <UserMemberMainWrapper>
          <DepartmentOngoingApplication
            departmentId={onClickValue?._id}
            admissionId={
              departmentProfile?.department?.institute?.admissionDepart?.[0]
            }
            // admissionId="651ba377e39dbdf817dd5291"
            urlOffSet="NORMAL"
          />
        </UserMemberMainWrapper>
      ) : searchParams.get("a") === "ongoing-detail" ? (
        <UserMemberMainWrapper>
          <OneOngoingDepartmentConfig
            departmentId={onClickValue?._id}
            admissionId={
              departmentProfile?.department?.institute?.admissionDepart?.[0]
            }
            financeId={
              departmentProfile?.department?.institute?.financeDepart?.[0]
            }
            // admissionId="651ba377e39dbdf817dd5291"
            // financeId=""
            urlOffSet="NORMAL"
          />
        </UserMemberMainWrapper>
      ) : searchParams.get("a") === "selected-subject" ? (
        <UserMemberMainWrapper>
          <ApplicationSubjectListDetail urlOffSet="NORMAL" />
        </UserMemberMainWrapper>
      ) : (
        <div className={style.departmenthead}>
          {!getQuery?.search && (
            <DepartmentHeadProfile
              departmentProfile={departmentProfile?.department}
              // onReftech={departmentProfileRefetch}
              carryParentState={carryParentState}
              instituteId={departmentProfile?.department?.institute?._id}
              did={onClickValue?._id}
            />
          )}

          {getQuery?.search && (
            <>
              {getQuery?.search?.substring(3) === "staff" && (
                <StaffRoom did={departmentProfile?.department?._id} />
              )}
              {getQuery?.search?.substring(3, 7) === "exam" && (
                <Examinations
                  did={departmentProfile?.department?._id}
                  bid={departmentProfile?.department?.userBatch?._id}
                  carryParentState={carryParentState}
                  instituteId={departmentProfile?.department?.institute?._id}
                />
              )}

              {getQuery?.search?.substring(3) === "fees" && (
                <Fees
                  did={departmentProfile?.department?._id}
                  bid={departmentProfile?.department?.userBatch?._id}
                />
              )}

              {getQuery?.search?.substring(3) === "holiday" && (
                <DepartmentHoliday did={onClickValue?._id} />
                // <Holiday
                //   did={departmentProfile?.department?._id}
                //   bid={departmentProfile?.department?.userBatch?._id}
                // />
              )}

              {/* {getQuery?.search?.substring(3) === "competition" && (
          <Competition
            did={departmentProfile?.department?._id}
            bid={departmentProfile?.department?.userBatch?._id}
          />
        )} */}

              {getQuery?.search?.substring(3) === "complaint" && (
                <DepartmentComplaint did={departmentProfile?.department?._id} />
              )}

              {/* {getQuery?.search?.substring(3) === "setting" && (
          <DepartmentSetting did={departmentProfile?.department?._id} />
        )} */}
              {getQuery?.search?.substring(3) === "setting" && (
                <DepartmentExamSetting
                  did={departmentProfile?.department?._id}
                  ea={departmentProfile?.department?.external_assesment}
                  ia={departmentProfile?.department?.internal_assesment}
                />
              )}
              {getQuery?.search?.substring(3) === "checklist" && (
                <Checklist
                  did={departmentProfile?.department?._id}
                  bid={departmentProfile?.department?.userBatch?._id}
                />
              )}

              {getQuery?.search?.substring(3, 10) === "backlog" && (
                <Backlogs
                  did={departmentProfile?.department?._id}
                  carryParentState={carryParentState}
                  instituteId={departmentProfile?.department?.institute?._id}
                  viewAs="DEPARTMENT_BACKLOG"
                />
              )}
              {getQuery?.search?.substring(3, 9) === "mentor" && (
                <DepartmentMentor
                  did={departmentProfile?.department?._id}
                  instituteId={departmentProfile?.department?.institute?._id}
                  carryParentState={carryParentState}
                />
              )}
              {getQuery?.search?.substring(3) === "statistics" && (
                <UserMemberContainerWrapper>
                  <UserMemberContentWrapper>
                    <StudentStatitics
                      departmentData={departmentProfile?.department}
                      instituteId={
                        departmentProfile?.department?.institute?._id
                      }
                      batchId={departmentProfile?.department?.userBatch?._id}
                      isFlow="DEPARTMENT"
                      parentUrl={`/q/${params.username}/member/department/${params.deptname}?a=statistics-detail`}
                      isUniversal="NO"
                    />
                  </UserMemberContentWrapper>
                </UserMemberContainerWrapper>
              )}

              {getQuery?.search?.substring(3) === "statistics-detail" && (
                <StudentStatiticsDetail />
              )}
              {/* {getQuery.search?.substring(3) === "class&students" && (
          <OneDepartmentClass />
        )} */}

              {(getQuery.search?.substring(3, 8) === "class" ||
                getQuery.search?.substring(3, 10) === "subject" ||
                getQuery.search?.substring(3, 13) === "assignment") && (
                <OneDepartmentClassDetail
                  carryParentState={carryParentState}
                  viewAs="DEPARTMENT_HEAD"
                  did={departmentProfile?.department?._id}
                  instituteId={departmentProfile?.department?.institute?._id}
                />
              )}
              {getQuery?.search?.substring(3) === "co-attainment" && (
                <DepartmentPo did={departmentProfile?.department?._id} />
              )}
              {getQuery?.search?.substring(3, 8) === "store" && (
                <DepartmentGoodsStore
                  departmentId={onClickValue?._id}
                  stid={
                    departmentProfile?.department?.institute?.storeDepart?.[0]
                  }
                />
              )}
            </>
          )}
        </div>
      )}
    </Suspense>
  );
}

export default DepartmentHead;
